import React, { useContext, useEffect } from 'react'
import style from './Navbar.module.css'
import { Link, useNavigate } from 'react-router-dom'
import logo from '../../Assets/Images/logo.png'
import { TokenCotext } from '../Context/TokenContext'
import { CartContext } from '../Context/CartContext'

export default function Navbar() {

  let {cartNumber, SetCartNumber , getCart} =useContext(CartContext)


  let {token, setToken} = useContext(TokenCotext)
  let navigate = useNavigate()
  function logout(){
    localStorage.removeItem("userToken")
    setToken(null)
    navigate("/login")
  }

  
// to view count in cart icon when directlly
// first add SetCartNumber,getCart in useContext
  useEffect(()=>{

    (async()=>{
      let data = await getCart()
      console.log(data.data.numOfCartItems);
      SetCartNumber(data.data.numOfCartItems)
    })()

  },[])

  return (
    <>
  <nav className="navbar navbar-expand-md  bg-body-tertiary">
    <div className="container-fluid">
      <Link className="navbar-brand" href="#">
        <img src={logo} className="d-block img-logo" style={{width:"130px"}} alt="..."  focusable="false" />
      </Link>
      <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarCollapse" aria-controls="navbarCollapse" aria-expanded="false" aria-label="Toggle navigation">
        <span className="navbar-toggler-icon"></span>
      </button>
      <div className="collapse navbar-collapse" id="navbarCollapse">
        {token?
         <ul className="navbar-nav me-auto mb-2 mb-md-0">
         <li className="nav-item">
           <Link className="nav-link active" aria-current="page" to="/react-Ecommerce">Home</Link>
         </li>
         <li className="nav-item">
           <Link className="nav-link" to="products"> Products</Link>
         </li>
         <li className="nav-item">
           <Link className="nav-link" to="cart"> cart</Link>
         </li>
         <li className="nav-item">
           <Link className="nav-link" to="categoray"> Categories</Link>
         </li>
         <li className="nav-item">
           <Link className="nav-link" to="brands"> Brands</Link>
         </li>
         <li className="nav-item">
           <Link className="nav-link" to="wishlist"> Wish List</Link>
         </li>
       </ul>
       :" "
      }
       

        <ul className="navbar-nav ms-auto mb-2 mb-md-0">

        {token?

          <>
           <li className="nav-item">
           <Link className="nav-link" to="wishlist"> 
           <i className='fa-solid fa-heart wishlist-style'></i>
           {/* <i class="fa-solid fa-heart"></i> */}
           </Link>
         </li>

          <li className="nav-item">
           <Link className="nav-link" to="cart"> 
           <i className='fa-solid fa-shopping-cart'></i>
           <span className='badge bg-main text-white'>{cartNumber}</span>
           </Link>
         </li>

         <li className="nav-item">
            <Link onClick={logout} className="nav-link" to="#"> LogOut</Link>
          </li>
          
          </>
         

          :
            <>
            <li className="nav-item">
            <Link className="nav-link " to="register">Register</Link>
          </li>
          <li className="nav-item">
            <Link className="nav-link" to="login"> Login</Link>
          </li>
            </>

        }
          
        
      
        </ul>
       
      </div>
    </div>
  </nav>

    </>
  )
}
