import React, { useContext, useEffect, useState } from 'react'
import style from './Cart.module.css'
import { CartContext } from '../Context/CartContext'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { BallTriangle } from 'react-loader-spinner'

export default function Cart() {
  const [data, setData] = useState([])
  const [isLoading, setIsLoading] = useState(true)
  const [cartPrice, setCartPrice] = useState([])

  let {getCart,SetCartNumber,DeleteCart, UpdateCart} = useContext(CartContext)

  useEffect(()=>{
    (async()=>{
      let data = await getCart()
      // console.log(data.data.data.products);
      setData(data.data.data.products)
      setCartPrice(data.data.data.totalCartPrice)
      setIsLoading(false)
    })()

  },[])



  // delete 

  async function removeProduct(id){
    let data =await DeleteCart(id)
    setData(data.data.data.products)
    // getCart()
    SetCartNumber(data.data.numOfCartItems)
    setIsLoading(false)
  }


  //update
async function updateProduct(id,count){
  if(count == 0){
    DeleteCart(id)
  }else{
      let data = await UpdateCart(id,count)
  setData(data.data.data.products)
  SetCartNumber(data.data.numOfCartItems)
  setIsLoading(false)
  }
}

  return (
    <div className='container'>
      {isLoading?
        <div  className='loading-style'>
        <BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass=""
        visible={true}
        /></div>
        
    :""}
      
      <div className="mt-3 d-flex justify-content-between">
      <h2 className=''>Shoppqing Cart</h2>
      <Link to={"/checkout"} className="btn bg-main text-light">Online Payment</Link>
      </div>
      <div className="row">
        <div className="col-md-11 bg-main-light shadow p-5 m-auto my-5 ">
          <h3><span className='text-main fw-bold'>Total Price </span>{cartPrice}</h3>
          {data.map((product)=>{
            return <div key={product.product.id} className="row border-bottom py-5 ">
              <div className="col-md-1">
                <img src={product.product.imageCover} className='w-100' alt="imageCover" />
              </div>
              <div className="col-md-11 d-flex justify-content-between align-items-center">
                <div>
                  <h5>{product.product.title}</h5>
                  <p>{product.price}</p>
                  <button onClick={()=>{removeProduct(product.product._id)}} className='btn btn-outline-danger'><i className='fa-regular fa-trash-can'></i>Remove</button>
                </div>
                <div>
                  <button onClick={()=>updateProduct(product.product._id,product.count+1)} className='btn btn-outline-success'>+</button>
                  <span className='mx-2'>{product.count}</span>
                  <button onClick={()=>updateProduct(product.product._id,product.count-1)} className='btn btn-outline-success'>-</button>
                </div>
              </div>
            </div>
          })}
        </div>
      </div>
      
    </div>
  )
}
