import axios from 'axios'
import { Formik, useFormik } from 'formik'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
export default function ForgotPassword() { 
  
    // const[successMessage, setSuccessMessage]= useState(null)
    // const[errorMessage, setErorMessage]= useState(null)
    const[isloading, setIsloading]= useState(false)
  
    let navigate =useNavigate()
  
    let ForgotPasswordSchema = Yup.object(
        {
            email:Yup.string().email("email not valid").required("email is required")
        }
    )
    async function Forgot(values){
        setIsloading(true)
        let {data} = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/forgotPasswords",values)

        console.log(data);
        if(data.statusMsg == 'success'){
                document.querySelector('.ForgotPassword').classList.add('d-none')
                document.querySelector('.VerfiyCode').classList.remove('d-none')
        }
     
  }
    let formik=useFormik({
        initialValues:{
            email:""
        } ,
        validationSchema:ForgotPasswordSchema,
        onSubmit:Forgot
    })
  





// ------------------------VerfiyCode--------------------

  
    let VerfiyCodeSchema = Yup.object(
        {
            resetCode:Yup.string().required("resetCode is required")
        }
    )
    async function VerfiyCode(values){
        
        let {data} = await axios.post("https://ecommerce.routemisr.com/api/v1/auth/verifyResetCode",values)
        console.log(data);
        // setIsloading(true)
        if(data.status== 'Success'){
            // setIsloading(true)
            navigate('/resetPassword')
        }

  }
    let VerfiyCodeformik=useFormik({
        initialValues:{
            resetCode:""
        } ,
        validationSchema:VerfiyCodeSchema,
        onSubmit:VerfiyCode
    })
  
  
  
  
  
    return (
      <>
        
      <div className="container mt-1 ForgotPassword" style={{marginBottom:" 100px"}}>
      <h2 className=' text-center text-main mx-auto mt-5'>Forgot Password Form</h2>
  
          <div className="w-75 border p-4 mx-auto ">
              <form onSubmit={formik.handleSubmit}>
  
                  <label htmlFor="email">email:</label>
                  <input type="email" name="email" id="email" className='form-control mb-2' 
                  value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                  {formik.touched.email && formik.errors.email ? <p className=" text-danger">{formik.errors.email}</p> : ""}

                   <div className="d-flex justify-content-end">
                  {isloading?<button className='btn btn-success ml-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button> 
                :<button disabled={!(formik.isValid && formik.dirty)} className='btn btn-success ml-auto' type='submit'>Send Code</button>}
                  </div>
              </form>
          </div>
      </div> 




      <div className="container mt-1 VerfiyCode d-none" style={{marginBottom:" 100px"}}>
      <h2 className=' text-center text-main mx-auto mt-5'>Verfiy Code Form</h2>
  
          <div className="w-75 border p-4 mx-auto ">
              <form onSubmit={VerfiyCodeformik.handleSubmit}>
  
                  <label htmlFor="resetCode">resetCode:</label>
                  <input type="text" name="resetCode" id="resetCode" className='form-control mb-2' 
                  value={VerfiyCodeformik.values.resetCode} onChange={VerfiyCodeformik.handleChange} onBlur={VerfiyCodeformik.handleBlur}/>
                  {VerfiyCodeformik.touched.resetCode && VerfiyCodeformik.errors.resetCode ? <p className=" text-danger">{VerfiyCodeformik.errors.resetCode}</p> : ""}
              
                   <div className="d-flex justify-content-end">
                  {/* {isloading? */}
                  {/* <button className='btn btn-success ml-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button>  */}
                  
                {/* : */}
                <button className='btn btn-success ml-auto' type='submit'>Verfiy Code</button>
                {/* } */}
                  
                
                
                
                  </div>
  
                  
              </form>
          </div>
      </div> 
      </>
    )
  }
  