import React, { useContext, useEffect, useState } from 'react'
import { CategiorayContext } from '../Context/CategoryContext'
import { BallTriangle } from 'react-loader-spinner'
import { Link } from 'react-router-dom'
import Slider from 'react-slick';

export default function Categoray() {

  let {getCategioray} = useContext(CategiorayContext)
  const [data, setData] = useState([])
  const [isLoading, setIsLoading] = useState(true)

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    autoplay:true,
    arrows:false,
  };

  useEffect(()=>{
    (async()=>{
        let {data} = await getCategioray()
        setData(data.data)
        console.log(data.data);
        setIsLoading(false)
    })()
},[])
  

  
  
return (
  <>



  

        <div className="container">
        <h2>Shop Popular Categories</h2>

        <div className="row">
        {isLoading?
              <div  className='loading-style'>
              <BallTriangle
              height={100}
              width={100}
              radius={5}
              color="#4fa94d"
              ariaLabel="ball-triangle-loading"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
              /></div> 
            :""}
            {data.map((el)=><div key={el.id} className="border-bottom col-md-2 col-sm-4 col-6">
                <div className="product py-2 px-3 mt-2">
                    {/* <Link to={`` + el._id} className='LinkDecoration'> */}
                        <img src={el.image} className='w-100' height={200} alt={el.name} />
                        <p className='text-main text-center'>{el.name}</p>
                        
                    {/* </Link> */}
                    
                </div>
                
            </div>
            )}
        </div>
    </div> 
    
  </>
)
}
