import React, { useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { CartContext } from '../Context/CartContext'

export default function AllOrders() {
  let {SetCartNumber} = useContext(CartContext)
  SetCartNumber(0)
  let navigate = useNavigate()
  navigate("/react-Ecommerce")
  return (
    <div>
        <div class="modal modal-sheet position-static d-block bg-body-secondary p-4 py-md-5" tabindex="-1" role="dialog" id="modalSheet">
  <div class="modal-dialog" role="document">
    <div class="modal-content rounded-4 shadow">
      <div class="modal-header border-bottom-0">
        <h1 class="modal-title fs-5">Order Alert</h1>
        {/* <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button> */}
      </div>
      <div class="modal-body py-0">
        <p>Your Order Sent successfully </p>
      </div>
      <div class="modal-footer flex-column align-items-stretch w-100 gap-2 pb-3 border-top-0">
        <Link to="">
        <button type="button" class="btn bg-main text-white" data-bs-dismiss="modal">Back to Home
        
        </button>
        </Link>
        
      </div>
    </div>
  </div>
</div>
    </div>
  )
}
