import axios from "axios";
import { createContext, useState } from "react";
import toast from "react-hot-toast";



export let CartContext =  createContext()

export default function CartContextProvider(props){
    const [cartNumber , SetCartNumber]=useState(0)
    
    let token = {
            token:localStorage.getItem("userToken")
        }


    function addToCart(id){
    console.log("id", id);
    return axios.post("https://ecommerce.routemisr.com/api/v1/cart",{
        productId:id
    },{
        headers: token    
    })
    
    }


    function getCart(id){
        console.log("id", id);
        return axios.get("https://ecommerce.routemisr.com/api/v1/cart",{
            headers: token    
        })
        
        }

        function DeleteCart(id){
            console.log("id", id);
            return axios.delete(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,{
                headers: token    
            })
            // console.log(id);
            }


            function UpdateCart(id, count){
                console.log("id", id);
                return axios.put(`https://ecommerce.routemisr.com/api/v1/cart/${id}`,{
                    count:count
                },{
                    headers: token    
                })
                // console.log(id);
                }


                function CheckOutPayment(id, FormData){
                    return axios.post(`https://ecommerce.routemisr.com/api/v1/orders/checkout-session/${id}?url=https://hatemalsaidi.github.io/react-Ecommerce`,
                    {
                        ShippingAddress:FormData
                    },{
                        headers:token
                    })
                }
    return <CartContext.Provider value={{addToCart, cartNumber, SetCartNumber,getCart,DeleteCart, UpdateCart, CheckOutPayment}}>
        {props.children}
    </CartContext.Provider>
    
}