import axios from "axios";
import { createContext } from "react";


export let CategiorayContext = createContext()
export default function CategiorayContextProvider(props){

    function getCategioray(){
        return axios.get("https://ecommerce.routemisr.com/api/v1/categories" )
    }
    return <CategiorayContext.Provider value={{getCategioray}}>
    {props.children}
    </CategiorayContext.Provider>
}