import axios from 'axios'
import { useFormik } from 'formik'
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'

export default function ResetPassword() {
    let navigate =useNavigate()

    const[isloading, setIsloading]= useState(false)

    async function resetPass(values){
        // setIsloading(true)
        let {data} = await axios.put("https://ecommerce.routemisr.com/api/v1/auth/resetPassword",values)

        console.log(data);
        if(data.token){
            navigate('/login')
        }
    }
    let formik = useFormik({
        initialValues:{
            email:"",
            newPassword: ""
        },onSubmit:resetPass
    })
  return (
    <>
        <div className="container mt-1 ForgotPassword" style={{marginBottom:" 100px"}}>
      <h2 className=' text-center text-main mx-auto mt-5'>Reset Password Form</h2>
  
          <div className="w-75 border p-4 mx-auto ">
              <form onSubmit={formik.handleSubmit}>
  
                  <label htmlFor="email">email:</label>
                  <input type="email" name="email" id="email" className='form-control mb-2' 
                  value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                  {formik.touched.email && formik.errors.email ? <p className=" text-danger">{formik.errors.email}</p> : ""}
              

                  <label htmlFor="newPassword">newPassword:</label>
                  <input type="password" name="newPassword" id="newPassword" className='form-control mb-2' 
                  value={formik.values.newPassword} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                  {formik.touched.newPassword && formik.errors.newPassword ? <p className=" text-danger">{formik.errors.newPassword}</p> : ""}
               

                   <div className="d-flex justify-content-end">
                  {isloading?<button className='btn btn-success ml-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button> 
                :<button disabled={!(formik.isValid && formik.dirty)} className='btn btn-success ml-auto' type='submit'>Reset Password</button>}
                  </div>
  
                  
              </form>
          </div>
      </div> 


    </>
  )
}
