import axios from 'axios';
import React from 'react'
import {useQuery } from 'react-query'
import Slider from 'react-slick';

export default function CategorySlider() {

    // slider setting variables
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 7,
    slidesToScroll: 1,
    autoplay:true,
    arrows:false,
  };

    // async function GetProductsDetails(id){
    //     let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/`+id)
    //     console.log(data.data);
    //     setDetails(data.data)
    //     setIsLoading(false)
    // }

    // useEffect(()=>{
    //     GetProductsDetails(id)
    // }
    // ,[])

    function GetCategory(){
        return axios.get("https://ecommerce.routemisr.com/api/v1/categories")
   
    }
    let {data, isLoading, isFetching}= useQuery("MainCategory", GetCategory)
    // let {data, isLoading, isFetching}= useQuery("FeatureProducts", GetCategory)
    // console.log(data);
        // console.log(data?.data?.data); 
        //عندما تكون جاهزه اجلبها
    
        
  return (
    <>
      <div className="container py-5">
        <h2>Shop Popular Categories</h2>
      <Slider {...settings}>
              {data?.data?.data.map((el)=> <div key={el._id} className='col-md-3 col-sm-4 col-6'>
                    <img src={el.image} height={200} className='w-100' alt="" />
                    <p className='text-center'>{el.name}</p>
                    </div>
                )}     </Slider>
            
                

               
           
      </div>
    </>
  )
}
