import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { BallTriangle } from 'react-loader-spinner';
import { useQuery } from 'react-query';
import { Link } from 'react-router-dom';

export default function Brands() {
    // const [data, setData] = useState([])
    // const [isLoading, setIsLoading] = useState(true)

    
    function getBreands(){
        return axios.get("https://ecommerce.routemisr.com/api/v1/brands")
    }
    let {data, isLoading, isFetching}= useQuery("Brands", getBreands)
        // console.log(data?.data?.data);
        //عندما تكون جاهزه اجلبها
    

//     async function getBreands(){
//         let {data} = await axios.get("https://ecommerce.routemisr.com/api/v1/brands")
//         // console.log(data.data);
//         setData(data.data);

//         setIsLoading(false)
//     }
//   useEffect(()=>{
//     getBreands()
//     }
//     ,[])
  return (
    <>
     <div className="container">
        <div className="row">
        {isLoading?
              <div  className='loading-style'>
              <BallTriangle
              height={100}
              width={100}
              radius={5}
              color="#4fa94d"
              ariaLabel="ball-triangle-loading"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
              /></div> 
            :""}



            {data?.data?.data.map((el)=><div key={el._id} className="border-bottom col-md-2 col-sm-4 col-6">
                <div className="product py-2 px-3 mt-2">
                    
                        <img src={el.image} className='w-100' alt={el.name} />
                        <p className='text-main text-center'>{el.name}</p>
                        
                   
                    
                </div>
                
            </div>
            )}
        </div>
    </div> 
    </>
  )
}
