import React, { useContext, useEffect, useState } from 'react'
import { useFormik } from 'formik'
import * as Yup from 'yup';
// import axios from 'axios'
import axios from 'axios'
import { CartContext } from '../Context/CartContext';


export default function CheckOut() {
    const [cartId , setCartId] = useState('')
    let {CheckOutPayment,getCart} = useContext(CartContext)


    useEffect(()=>{

        (async()=>{
          let data = await getCart()
          console.log(data.data.data._id);
          setCartId(data.data.data._id)
        })()
    
      },[])
    

    // let id = ''
    // const[successMessage, setSuccessMessage]= useState(null)
    // const[errorMessage, setErorMessage]= useState(null)
    // const[isloading, setIsloading]= useState(false)



    let formik=useFormik({
        initialValues:{
            details: "",
            city:"",
            phone:""
        } ,
        // validationSchema:mySchema,
        onSubmit:(values)=>{
            Payment(values);
        }
    })

async function Payment(values){
        // setIsloading(true)
 let data = await CheckOutPayment(cartId,values)
    console.log(data);
        if(data.stutes= 'success'){
            console.log(data.data);
            window.location= data.data.session.url
            // setSuccessMessage(date.data.message)
            // navigate("/login")
            // setIsloading(false)
           
         }
       
        
       
    }
  return (
    <>

    <div className="container mt-1 PaymentClass" style={{marginBottom:" 100px"}}>
        <div className="w-75 border p-3 mx-auto mt-5 shadow">
            <form onSubmit={formik.handleSubmit}>{/* to run formik  onSubmit={formik.handleSubmit} */}
                <h2>Payment Form:</h2>
                {/* msg success  */}
                {/* {successMessage? <div className='aletr alert-success'>{successMessage}</div>:" "} */}

                {/* msg error */}
                {/* {errorMessage? <div className='alert alert-danger'>{errorMessage}</div>:" "} */}


                <label htmlFor="details">details:</label>
                <input type="text" name="details" id="details" className='form-control mb-2' 
                value={formik.values.details} onChange={formik.handleChange} />
                {/* {formik.touched.email && formik.errors.email ? <div className="alert alert-danger">{formik.errors.email}</div> : ""} */}
            
                <label htmlFor="city">city:</label>
                <input type="text" name="city" id="city" className='form-control mb-2'
                 value={formik.values.city} onChange={formik.handleChange} />
                 {/* {formik.touched.password && formik.errors.password ? <div className="alert alert-danger">{formik.errors.password}</div> : ""} */}
            

                <label htmlFor="phone">phone:</label>
                <input type="tel" name="phone" id="phone" className='form-control mb-2' 
                value={formik.values.phone} onChange={formik.handleChange} />
                {/* {formik.touched.phone && formik.errors.phone ? <div className="alert alert-danger">{formik.errors.phone}</div> : ""} */}
            
                {/* <button className='btn btn-success me-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button>  */}
            
                <button  className='btn btn-success me-auto' type='submit'>Payment</button>
              
                
                
            </form>
        </div>
    </div> 
    </>
  )
}
