import React, { useContext, useState } from 'react'
import style from './Register.module.css'
import { useFormik } from 'formik'
import * as Yup from 'yup';
// import axios from 'axios'
import axios from 'axios'
import { Link, useNavigate } from 'react-router-dom';
import { counterContext } from '../Context/CounterContext';



export default function Register() {
    let {conter, setcounter} = useContext(counterContext);
    const[successMessage, setSuccessMessage]= useState(null)
    const[errorMessage, setErorMessage]= useState(null)
    const[isloading, setIsloading]= useState(false)

    let navigate =useNavigate()

    let mySchema = Yup.object(
        {
        
            name: Yup.string().required("name is required").min(5,"min 5 char").max(15,"max 15 char"),
            email:Yup.string().email("email not valid").required("email is required"),
            // password:Yup.string().required("password is required").matches(/^[A-Z][a-z0-9]{3,8}4/,"password must start with capital litter"),
            password:Yup.string().required("password is required"),
            rePassword:Yup.string().required("password is required").oneOf([Yup.ref("password")], "password not match"),
            // phone:Yup.string().required("phone is required").matches(/^[A-Z][a-z0-9]{3,8}4/,"Yemen's Number is required")
            phone:Yup.string().required("phone is required")
        }
    )

    let formik=useFormik({
        initialValues:{
            name: "",
            email:"",
            password:"",
            rePassword:"",
            phone:""
        } ,
        validationSchema:mySchema,
        onSubmit:(values)=>{
            FormRegister(values);
        }
    })

    // async function FormRegister(values){
    //     console.log(values);
    //     let {data} =await  axios.post("https://ecommerce.routemisr.com/api/v1/auth/signup", values)
    //     console.log(data);
    //     if(data.message= 'success'){
    //         //login
    //         setsuccessMessage(data.message)
    //         navigate("/")
    //     }
    // }

   async function FormRegister(values){
        // console.log(values);
        setIsloading(true)


        return  axios.post("https://ecommerce.routemisr.com/api/v1/auth/signup", values).then((date)=>{
            // console.log(data);
        if(date.data.message= 'success'){
            //login
            setSuccessMessage(date.data.message)
            navigate("/login")
            setIsloading(false)
         }
        }).catch((err)=>{
            console.log(err);

            // error bath in api is response.data.message
            setErorMessage(err.response.data.message);
            setIsloading(false)
        
        })
        
       
    }
  return (
    <>

    <div className="container mt-1 RegisterClass" style={{marginBottom:" 100px"}}>
    <h2 className=' text-center text-main mx-auto mt-5'>Register Form</h2>
        <div className="w-75 border p-4 mx-auto">
            <form onSubmit={formik.handleSubmit}>{/* to run formik  onSubmit={formik.handleSubmit} */}
                {/* msg success  */}
                {successMessage? <div className='aletr alert-success'>{successMessage}</div>:" "}

                {/* msg error */}
                {errorMessage? <div className='alert alert-danger'>{errorMessage}</div>:" "}

                <label htmlFor="name">name:</label>
                <input type="text" name="name" id="name" className='form-control mb-2' 
                value={formik.values.name} onChange={formik.handleChange}  onBlur={formik.handleBlur} /> 
                {formik.touched.name && formik.errors.name ? <div className="alert alert-danger">{formik.errors.name}</div> : ""} 
            
                <label htmlFor="email">email:</label>
                <input type="email" name="email" id="email" className='form-control mb-2' 
                value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                {formik.touched.email && formik.errors.email ? <div className="alert alert-danger">{formik.errors.email}</div> : ""}
            
                <label htmlFor="password">password:</label>
                <input type="password" name="password" id="password" className='form-control mb-2'
                 value={formik.values.password} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                 {formik.touched.password && formik.errors.password ? <div className="alert alert-danger">{formik.errors.password}</div> : ""}
            
                <label htmlFor="rePassword">Re-Password:</label>
                <input type="password" name="rePassword" id="rePassword" className='form-control mb-2' 
                value={formik.values.rePassword} onChange={formik.handleChange} onBlur={formik.handleBlur} />
                {formik.touched.rePassword && formik.errors.rePassword ? <div className="alert alert-danger">{formik.errors.rePassword}</div> : ""}
            
                <label htmlFor="phone">phone:</label>
                <input type="tel" name="phone" id="phone" className='form-control mb-2' 
                value={formik.values.phone} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                {formik.touched.phone && formik.errors.phone ? <div className="alert alert-danger">{formik.errors.phone}</div> : ""}
                
                <div className="d-flex justify-content-end">
                    {isloading?<button className='btn btn-success ml-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button> 
                :<button disabled={!(formik.isValid && formik.dirty)} className='btn btn-success ml-auto' type='submit'>Register</button>}
                </div>
                
                
                <p className='text-muted'>I Have a account <Link className='text-main fw-bold' to='/login'>Login</Link></p>
                
            </form>
        </div>
    </div> 
    </>
  )
}
