import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { DNA } from 'react-loader-spinner'
import { useQuery } from 'react-query'
import { useParams } from 'react-router-dom'
import Slider from 'react-slick'
import { CartContext } from '../Context/CartContext'
import toast from 'react-hot-toast'

export default function ProductDetails() {

  const [details , setDetails]=useState({})
  const [isLoading , setIsLoading]=useState(true)

// slider setting variables
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  // get id when press any product in product page 
   let {id} = useParams();

 async function GetProductsDetails(id){
        let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/`+id)
        console.log(data.data);
        setDetails(data.data)
        setIsLoading(false)
    }

    useEffect(()=>{
        GetProductsDetails(id)
    }
    ,[])


        //start  addToCart
let {addToCart,SetCartNumber} = useContext(CartContext)

async function addFromProductDetails(id){
    let {data}= await addToCart(id)
    if(data.status=='success'){
        SetCartNumber(data.numOfCartItems)
        toast.success('Product Added Successfully!')
    }else{
        toast.error("This didn't work.")
    }

}
 //end  addToCart


//   let {data, isLoading, isFetching}  = useQuery("ProductDetails",()=>GetProductsDetails(id) )
//   console.log(data?.data?.data);
  return (
    <>
      <div className="container">
    
      

          {/* loading */}
          {isLoading?
            <DNA
            visible={true}
            height="80"
            width="80"
            ariaLabel="dna-loading"
            wrapperStyle={{}}
            wrapperClass="dna-wrapper"
            />
            // <Bars  height="80" width="80" color="#1c245a" 
            // ariaLabel="bars-loading" wrapperStyle={{}} 
            // wrapperClass="" visible={true} /> 
            :<div className="row align-items-center text-center">
           
           
           <div className="col-md-4 py-3" >
                {/* <img src={details.imageCover} className='w-100' alt="" /> */}
                <Slider {...settings}>
                    {details.images.map((el)=><img src={el} className='w-75'/>)}
                  </Slider>
            </div>
            <div className="col-md-8">
                <h3>{details.title}</h3>
                <p>{details.description}</p>
                <div className="d-flex justify-content-between">
                    <p>{details.price} RIAL</p>
                    <p><i className='fa fa-star rating-color'></i>{details.ratingsAverage}</p>
                </div>
                <button onClick={()=>addFromProductDetails(details.id)} className='btn bg-main text-white w-100'>+ Add To Card</button>
            </div>
        </div>}








      </div>
    </>
  )
}
