import React, { useContext, useEffect, useState } from 'react'
import { WishListContext } from '../Context/WishListContext'
import { Link } from 'react-router-dom'
import { BallTriangle, DNA } from 'react-loader-spinner'
import toast from 'react-hot-toast'
import { CartContext } from '../Context/CartContext'
import { useQuery } from 'react-query'
import axios from 'axios'

export default function WishList() {
    let {getWishList, removefromWishList} = useContext(WishListContext)
    let{addToCart, SetCartNumber} = useContext(CartContext)
    const [data, setData] = useState([])
    const [isLoading, setIsLoading] = useState(true)


    useEffect(()=>{
        (async()=>{
            let {data} = await getWishList()
            setData(data.data)
            console.log(data.data);
            setIsLoading(false)
        })()
    },[])

   async function removefromWish(id){
        let {data} = await removefromWishList(id)
        if(data.status=="success"){
        console.log(id);
        console.log(data);
        setData(data.data)
        // setData(data?.data)
        // setIsLoading(false)
            toast.success('Removed from WishList Successfully!')
        }else{
            toast.error("This didn't work.")
        }
    }

  async function addFromWishToCart(id){
        let {data} = await addToCart(id)
        console.log(data);
        SetCartNumber(data.numOfCartItems)
        toast.success('Product Added Successfully!')
    }

  return (
    <>
    <div className="container ">
        {isLoading?
        <div  className='loading-style'>
        <BallTriangle
        height={100}
        width={100}
        radius={5}
        color="#4fa94d"
        ariaLabel="ball-triangle-loading"
        wrapperStyle={{}}
        wrapperClass=""
        visible={true}
        /></div>
        
    :""}
        <h2>Wish List</h2>
  {/* <p>The .table-striped class adds zebra-stripes to a table:</p>             */}
  <table className='table'>
    <thead>
      <tr>
        <th>Image Cover</th>
        <th>Product Name</th>
        {/* <th>Category</th> */}
        <th>Price</th>
        <th>Delete</th>
        <th>Add To Cart</th>
      </tr>
    </thead>
    <tbody>
        {/* {data.map((el)=><tr > */}
        {data.map((el)=><tr key={el._id} className='col-md-12 w-100'>
        <td><img src={el.imageCover} height={100} width={100}  alt={el.title} /></td>
        <td>{el.title}</td>
        {/* <td>{el.category.name}</td> */}
        <td>{el.price}</td>
        <td> <button onClick={()=>removefromWish(el.id)} className='btn btn-outline-danger'>Remove <i className='fa-regular fa-trash-can'></i></button></td>
        <td> <button onClick={()=>addFromWishToCart(el.id)} className='btn btn-outline-success'>Add To <i className='fa-solid fa-shopping-cart'></i></button></td>
      </tr>
        )} 
    
    </tbody>
  </table>
          
    </div>
      
    </>
  )
}
