import React, { useContext, useState } from 'react'
import style from './Login.module.css'
import { useFormik } from 'formik'
import * as Yup from 'yup';
// import axios from 'axios'
import axios from 'axios'
import { Link, useNavigate } from 'react-router-dom';
import { counterContext } from '../Context/CounterContext';
import { TokenCotext } from '../Context/TokenContext';

export default function Login() {
  // token context
  let {token, setToken} = useContext(TokenCotext)

  const[successMessage, setSuccessMessage]= useState(null)
  const[errorMessage, setErorMessage]= useState(null)
  const[isloading, setIsloading]= useState(false)

  let navigate =useNavigate()

  let mySchema = Yup.object(
      {
      
          email:Yup.string().email("email not valid").required("email is required"),
          // password:Yup.string().required("password is required").matches(/^[A-Z][a-z0-9]{3,8}4/,"password must start with capital litter"),
          password:Yup.string().required("password is required"),
      }
  )

  let formik=useFormik({
      initialValues:{
          email:"",
          password:""
      } ,
      validationSchema:mySchema,
      onSubmit:(values)=>{
          FormLogin(values);
      }
  })



  async function FormLogin(values){
    // console.log(values);
    setIsloading(true)


    return  axios.post("https://ecommerce.routemisr.com/api/v1/auth/signin", values).then((date)=>{
        // console.log(data);
    if(date.data.message= 'success'){
        //login
        setSuccessMessage(date.data.message)
        navigate("/")
        setIsloading(false)

        //save token
        setToken(date.data.token);
        localStorage.setItem("userToken", date.data.token)


     }
    }).catch((err)=>{
        console.log(err);

        // error bath in api is response.data.message
        setErorMessage(err.response.data.message);
        setIsloading(false)
    
    })
    
   
}



  return (
    <>
      
    <div className="container mt-1 RegisterClass" style={{marginBottom:" 100px"}}>
    <h2 className=' text-center text-main mx-auto mt-5'>Login Form</h2>

        <div className="w-75 border p-4 mx-auto ">
            <form onSubmit={formik.handleSubmit}>
                
                {/* msg success  */}
                {successMessage? <div className='aletr alert-success'>{successMessage}</div>:" "}

                {/* msg error */}
                {errorMessage? <div className='alert alert-danger'>{errorMessage}</div>:" "}

                <label htmlFor="email">email:</label>
                <input type="email" name="email" id="email" className='form-control mb-2' 
                value={formik.values.email} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                {formik.touched.email && formik.errors.email ? <div className="alert alert-danger">{formik.errors.email}</div> : ""}
            
                <label htmlFor="password">password:</label>
                <input type="password" name="password" id="password" className='form-control mb-2'
                 value={formik.values.password} onChange={formik.handleChange} onBlur={formik.handleBlur}/>
                 {formik.touched.password && formik.errors.password ? <div className="alert alert-danger">{formik.errors.password}</div> : ""}

                 <div className="d-flex justify-content-end">
                {isloading?<button className='btn btn-success ml-auto' type='submit'><i className='fa fa-spin fa-spinner'></i></button> 
                :<button disabled={!(formik.isValid && formik.dirty)} className='btn btn-success ml-auto' type='submit'>login</button>}
                </div>

                <p className='text-muted'>Forgot Password <Link className='text-main fw-bold' to='/ForgotPassword'>Reset Password</Link></p>
                <p className='text-muted'>I am not Have a account <Link className='text-main fw-bold' to='/register'>Register</Link></p>

                
            </form>
        </div>
    </div> 
    </>
  )
}
