import React, { useContext, useEffect, useState } from 'react'
import Home from './Components/Home/Home'
import Cart from './Components/Cart/Cart'
import Login from './Components/Login/Login'
import Register from './Components/Register/Register'
import Products from './Components/Products/Products'
import Layout from './Components/Layout/Layout'
import NotFound from './Components/NotFound/NotFound'
import Navbar from './Components/Navbar/Navbar'
import { RouterProvider, createBrowserRouter } from 'react-router-dom'
import { TokenCotext } from './Components/Context/TokenContext'
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute'
import ProtectedAuth from './Components/ProtectedAuth/ProtectedAuth'
import ProductDetails from './Components/ProductDetails/ProductDetails'
import style from '../src/App.css'
import CheckOut from './Components/CheckOut/CheckOut'
import AllOrders from './Components/AllOrders/AllOrders'
import WishList from './Components/WishList/WishList'
import Categoray from './Components/Categoray/Categoray'
import Brands from './Components/Brands/Brands'
import FeatureProducts from './Components/FeatureProducts/FeatureProducts'
import ForgotPassword from './Components/ForgotPassword/ForgotPassword'
import ResetPassword from './Components/ResetPassword/ResetPassword'

export default function App() {

  let {setToken} = useContext(TokenCotext)

  useEffect(()=>{
    if(localStorage.getItem("userToken")){
      setToken(localStorage.getItem("userToken"))
    }
  }
  ,[])

  let routes = createBrowserRouter([{
    path:"", element:<Layout/>,children:([
      {path:"login",element:<ProtectedAuth><Login/></ProtectedAuth>},
      {path:"register",element:<ProtectedAuth><Register/></ProtectedAuth>},
      {path:"forgotPassword",element:<ProtectedAuth><ForgotPassword/></ProtectedAuth>},
      {path:"resetPassword",element:<ProtectedAuth><ResetPassword/></ProtectedAuth>},

      {index:true,element:<ProtectedRoute><Home/></ProtectedRoute>},
      {path:"cart",element:<ProtectedRoute><Cart/></ProtectedRoute>},
      {path:"react-Ecommerce",element:<ProtectedRoute><Home/></ProtectedRoute>},
    
      {path:"products",element:<ProtectedRoute><FeatureProducts/></ProtectedRoute>},
      {path:"wishlist",element:<ProtectedRoute><WishList/></ProtectedRoute>},
      {path:"categoray",element:<ProtectedRoute><Categoray/></ProtectedRoute>},
      {path:"brands",element:<ProtectedRoute><Brands/></ProtectedRoute>},
      {path:"details/:id",element:<ProtectedRoute><ProductDetails/></ProtectedRoute>},
      // {path:"details:id",element:<ProtectedRoute><ProductDetails/></ProtectedRoute>},
      {path:"checkout",element:<ProtectedRoute><CheckOut/></ProtectedRoute>},
      {path:"/allorders",element:<ProtectedRoute><AllOrders/></ProtectedRoute>},

      {path:"*",element:<NotFound/>},
    ])
  }])
  return (
    <>
      <RouterProvider router={routes}></RouterProvider>
    </>
  )
}