import React from 'react'
import mainSlider1 from '../../Assets/Images/mainSlider1.jpg'
import mainSlider2 from '../../Assets/Images/mainSlider2.jpg'
import mainRight1 from '../../Assets/Images/portfolio-4.jpg'
import mainRight2 from '../../Assets/Images/portfolio-3.jpg'
import Slider from 'react-slick';
export default function MainSlider() {

        // slider setting variables
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay:true,
    arrows:false,
  };

  return (
    <>
       <div className="container">
        <div className="row gx-0">
            <div className="col-md-9">
            <Slider {...settings}>
                <img src={mainSlider2} height={400} className='w-100' alt="" />
                <img src={mainSlider1} height={400} className='w-100' alt="" />
            </Slider>
            </div>
            <div className="col-md-3">
            <img src={mainRight1} height={200} className='w-100' alt="" />
                <img src={mainRight2} height={200} className='w-100' alt="" />
            </div>
        </div>
      
            
                

               
           
      </div>
      
    </>
  )
}
