import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { BallTriangle, Bars, DNA } from 'react-loader-spinner'
import {useQuery } from 'react-query'
import { Link } from 'react-router-dom'
import { CartContext } from '../Context/CartContext'
// import { WishListContext } from '../Context/WishListContext'
import toast from 'react-hot-toast'
import { WishListContext } from '../Context/WishListContext'


export default function FeatureProducts() {
    // const [product , setProduct]=useState([]) // use state to save products data

    // const [isLoading , setIsLoading]=useState(true)
    // async function GetProducts(){
    //     let {data} = await axios.get("https://ecommerce.routemisr.com/api/v1/products")
    //     console.log(data);
    //     setProduct(data.data)
    //     setIsLoading(false)
    // }

    // useEffect(()=>{
    //     GetProducts()
    // }
    // ,[])


    //start  addToCart
let {addToCart,SetCartNumber} = useContext(CartContext)
let {addToWishList} = useContext(WishListContext)


async function addProductToCart(id){
    let {data}= await addToCart(id)
    if(data.status=='success'){
        SetCartNumber(data.numOfCartItems)
        toast.success('Product Added Successfully!')
    }else{
        toast.error("This didn't work.")
    }
}
 //end  addToCart


    function GetProducts(){
        return axios.get("https://ecommerce.routemisr.com/api/v1/products")
    }
    let {data, isLoading, isFetching}= useQuery("FeatureProducts", GetProducts)
        // console.log(data?.data?.data);
        //عندما تكون جاهزه اجلبها
        


        async function addToWish(id){
            let {data} = await addToWishList(id)
            if(data.status=='success'){
                toast.success('Added To WishList Successfully!')
            }else{
                toast.error("This didn't work.")
            }
        }
  return (
    <>
    <div className="container">
        <div className="row">
            {/* loading */}
            {isLoading?
              <div  className='loading-style'>
              <BallTriangle
              height={100}
              width={100}
              radius={5}
              color="#4fa94d"
              ariaLabel="ball-triangle-loading"
              wrapperStyle={{}}
              wrapperClass=""
              visible={true}
              /></div>
            // <Bars  height="80" width="80" color="#1c245a" 
            // ariaLabel="bars-loading" wrapperStyle={{}} 
            // wrapperClass="" visible={true} /> 
            :""}

            {/* use normal axios */}
            {/* {product.map((el)=>  <div key={el.id} className="col-md-2">*/}

            {/* use react query */}
            {data?.data?.data.map((el)=><div key={el.id} className="border-bottom col-md-2 col-sm-4 col-6">
                <div className="product py-2 px-3 mt-2 ">
                    {/* <Link to={`https://hatemalsaidi.github.io/details/` + el.id} className='LinkDecoration'> */}
                    <Link to={`/details/` + el.id} className='LinkDecoration'>
                    {/* <Link to={`http://localhost:3000/react-Ecommerce/details/` + el.id} className='LinkDecoration'> */}
                        <img src={el.imageCover} className='w-100' alt={el.title} />
                        <p className='text-main text-center'>{el.title.split(" ").splice(0,3).join(" ")}</p>
                        {/* <p>{el.description.split(" ").splice(0,3).join(" ")}</p> */}
                        <div className="d-flex justify-content-between">
                            <p>{el.price} Rial</p>
                            <p>{el.ratingsAverage}<i className='fa fa-star rating-color'></i></p>
                            
                        </div>
                    </Link>
                    <div className="d-flex justify-content-between ">
                        <button onClick={()=>addProductToCart(el.id)}  className='btn  text-dark  w-50'><i className='fa-solid addToCart-style fa-shopping-cart'></i></button>
                        <button onClick={()=>addToWish(el.id)}  className='btn text-dark w-50'><i class="fa-regular fa-heart wishlist-style"></i></button>
                    </div>
                    {/* <i class="fa-solid fa-heart"></i> */}
                </div>
                
            </div>
            )}
            
        </div>
    </div>
      
    </>
  )
}